import barba from '@barba/core';
import GSAP from 'gsap'


class App{

constructor(){

this.createBarba()
    
}

createBarba(){


this.allImages = document.querySelectorAll('main img')

const easeIn = (container,isDone)=>{

    return GSAP.to(container,{opacity:0,y:60,duration:1,ease:'Power4.in',clearProps:'all',onComplete:()=> isDone()})
}
const easeOut = (container)=>{
    
    return GSAP.from(container,{opacity:0,y:60,duration:1,ease:'Power4.inOut',clearProps:'all'})
}



barba.init({

transitions: [{
   
    leave({current}) {
        const isDone = this.async();
       easeIn(current.container,isDone) 
     
    },
    enter({next}) {
        easeOut(next.container)  
    }
  }]

})

}

}


new App()